.close_btn{
    width: fit-content;
    justify-self: end;
    align-self: center;
    padding: 10px 12px 10px 12px;
    background-color: white;
    border: 1px solid gray;
    color:#9D9D9D;
    border-radius: 12px;
    cursor: pointer;
  
  }
  
  
  .cart_item{
    display:grid;
    grid-template-columns: 1fr 3fr 1fr;
    border: 1px solid #9D9D9D;
    border-radius: 15px;
    padding:15px;
    margin-bottom: 30px;
  
  }
  
  .cart_img{
    width: 70px;
  }
 